.about__container {
  display: flex;
  gap: 3rem;
}
.about__me {
  height: fit-content;
  margin-bottom: 1rem;
  /* max-width: 1500px; */
  /* aspect-ratio: 1/1; */
  border-radius: 1rem;
  display: grid;
  place-items: center;
  background: linear-gradient(
    35deg,
    rgba(255, 0, 212, 0.596),
    rgba(0, 255, 255, 0.932)
  );
  transition: var(--transition);
}

img {
  max-height: 500px;
  width: 100%;
  border-radius: 1rem;
  border: 1px solid transparent;
  transform: rotate(15deg);
  transition: var(--transition);
}

img:hover {
  border: 5px solid rgb(0, 255, 255);
  transform: rotate(0deg);
}

.about__cards {
  display: flex;
  gap: 1.5rem;
}

.about__card {
  border: 3px solid rgba(255, 0, 212, 0.041);
  border-radius: 1rem;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about__card:hover {
  border: solid 3px var(--border-clr-alter);
  border-radius: 3rem;
  transition: var(--transition);
}

.about__icon {
  margin-right: 0.5rem;

  color: var(--color-primary);
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.about__card h3 {
  font-size: 1rem;
}

.aboutTxt {
  padding-top: 1rem;
  /* max-width: 1000px; */
  border-radius: 1.5rem;
  padding-bottom: 10rem;
}

p {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 0.5rem;
  text-indent: 1rem;
}

.bullets {
  margin: 1rem;
}

.li__child {
  font-style: italic;
  margin-left: 1.5rem;
  list-style-type: "тед ";
}
/* ============small device=============== */

