@media screen and (max-width: 500px) {
  section {
    display: grid;
    padding-top: 1.5rem;
  }
  .about__container {
    display: grid;
    gap: 2rem;
    max-width: 250px;
  }
  .about__me {
    max-width: 400px;
  }

  .about__cards {
    gap: 0.5rem;
  }

  .about__card {
    padding: 0.3rem;
  }

  .aboutTxt {
    padding-top: 1rem;

    color: var(--color-light);

    background-color: rgba(255, 248, 220, 0.308);

    border-radius: 1.5rem;
    padding-bottom: 6rem;
  }

  p {
    padding-left: 0.3rem;
    padding-right: 0.2rem;
    padding-bottom: 0.2rem;
    text-indent: 0.5rem;
  }
}

