@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,900;1,300;1,600&display=swap");

:root {
  --bg-card: gray;
  --border-clr: rgb(255, 0, 212);
  --border-clr-alter: rgb(0, 255, 255);

  --transition: all 700ms ease-in-out 25ms;
}

body {
  padding: 1rem;
  font-family: "Poppins", sans-serif;
  line-height: 1.7;
}

/* ============ Gen Styles =============== */

@media screen and (min-width: 800px) {
  h1,
  h2,
  h3,
  h4,
  h5 {
    font-weight: 500;
  }

  a {
    text-decoration: none;
    border-radius: 1rem;
    padding: 0.5rem;
    color: rgb(1, 240, 248);
    transition: var(--transition);
  }

  a:hover {
    color: rgb(250, 4, 229);
  }
}
